<template>
  <el-radio-group v-model="labelPosition" size="small">
  <el-radio-button label="left">左对齐</el-radio-button>
  <el-radio-button label="right">右对齐</el-radio-button>
  <el-radio-button label="top">顶部对齐</el-radio-button>
</el-radio-group>
<div style="margin: 20px;"></div>
<el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign" :rules="HoustRules" ref="House_split">
  <el-form-item prop="Cno" label="电话号码">
    <el-input v-model="formLabelAlign.Cno"></el-input>
  </el-form-item>
  <el-form-item prop="Cname" label="姓名">
    <el-input v-model="formLabelAlign.Cname"></el-input>
  </el-form-item>
  <el-form-item prop="Cdepartment" label="部门">
    <el-input v-model="formLabelAlign.Cdepartment"></el-input>
  </el-form-item>
  <el-form-item prop="Ctitle" label="职称">
    <el-input v-model="formLabelAlign.Ctitle"></el-input>
  </el-form-item>
  <el-form-item prop="Cnum_family" label="家庭人口">
    <el-input v-model="formLabelAlign.Cnum_family"></el-input>
  </el-form-item>
  <el-form-item prop="Crequirement" label="需求面积">
    <el-input v-model="formLabelAlign.Crequirement"></el-input>
  </el-form-item>
  <el-form-item class="btn">
          <el-button @click="submit" type="primary">提交</el-button>
        </el-form-item>
</el-form>
</template>

<script>
  export default {
    data() {
      return {
        labelPosition: 'right',
        formLabelAlign: {
          Cno: '',
          Cname: '',
          Cdepartment: '',
          Ctitle: '',
          Cnum_family: '',
          Crequirment: ''
        },
        HoustRules:{
          Cno:[
            {
            required: true,
            message: "请输入电话号码",
            trigger: "blur",
          },
          {
            min: 11,
            max: 11,
            message: "应为11个数字",
            trigger: "blur",
          },
          ],
          Cname:[
            {
            required: true,
            message: "请输入姓名",
            trigger: "blur",
          },
          ],
          Cdepartment: [
            {
            required: true,
            message: "请输入部门",
            trigger: "blur",
          },
          ],
          Ctitle:[
            {
            required: true,
            message: "请输入职称",
            trigger: "blur",
          },
          ],
          Cnum_family:[
            {
            required: true,
            message: "请输入家庭人口",
            trigger: "blur",
          },
          ],
          Crequirement:[
            {
            required: true,
            message: "请输入需求面积",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    submit() {
      this.$refs.House_split.validate(async (valid) => {
        if (!valid) return;
        const { data: res } = await this.$http.post(
          "api/consumer/register",
          this.formLabelAlign
        );
        if (res.code == 2000) {
          this.$message.error("此面积您不可租赁");
          return;
        }
        if (res.code == 2001) {
          this.$message.error("您已填写过，请等待分房");
          return;
        }
        if (res.code == 200) {
          this.$message.success("提交成功");
          // window.sessionStorage.setItem('username', res.data.user.StaffName);
          // window.sessionStorage.setItem('level', res.data.user.StaffLevel);
          // window.sessionStorage.setItem('salary', res.data.user.StaffSalary);
          // window.sessionStorage.setItem('remarks', res.data.user.StaffRemarks);
          // window.sessionStorage.setItem('telephone', res.data.user.StaffTelephone);
          // this.$router.push({ path: "/Home" });
          return;
        }
        this.$message.error("后台错误");
      });
    },
  }
}
</script>