<template>
  <div class="login-wrap">
    <div class="login_box">
      <h2 class="title">房产管理系统</h2>
      <el-form
        ref="loginFormRef"
        :model="loginForm"
        :rules="loginRules"
        class="login_form"
        label-width="0px"
      >
        <el-form-item prop="Aname">
          <el-input
            placeholder="请输入账号"
            v-model="loginForm.Aname"
            prefix-icon="el-icon-user"
          ></el-input>
        </el-form-item>
        <el-form-item prop="Apassword">
          <el-input
            placeholder="请输入密码"
            v-model="loginForm.Apassword"
            prefix-icon="el-icon-lock"
            type="password"
          ></el-input>
        </el-form-item>
        <el-form-item class="btn">
          <el-button @click="login" type="primary">登录</el-button>
          <el-button @click="resetLoginForm" type="info">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loginForm: {
        Aname: "",
        Apassword: "",
      },
      loginRules: {
        Aname: [
          {
            required: true,
            message: "请输入用户名",
            trigger: "blur",
          },
          {
            min: 5,
            max: 12,
            message: "长度在 5 到 12 个字符",
            trigger: "blur",
          },
        ],
        Apassword: [
          {
            required: true,
            message: "请输入密码",
            trigger: "blur",
          },
          {
            min: 6,
            max: 12,
            message: "密码为 6~12 位",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    resetLoginForm() {
      this.$refs.loginFormRef.resetFields();
    },
    login() {
      // if (this.loginForm.Aname == "keshe"){
      //   if (this.loginForm.Apassword == "keshe2021"){
      //     this.$message.success("登录成功");
      //     this.$router.push({ path: "/Home" });
      //     return;
      //   }
      // }
      this.$refs.loginFormRef.validate(async (valid) => {
        if (!valid) return;
        const { data: res } = await this.$http.post(
          "api/admint",
          this.loginForm
        );
        if (res.code == 1001) {
          this.$message.error("没有此用户");
          return;
        }
        if (res.code == 1002) {
          this.$message.error("密码错误");
          return;
        }
        if (res.code == 200) {
          this.$message.success("登录成功");
          // window.sessionStorage.setItem('username', res.data.user.StaffName);
          // window.sessionStorage.setItem('level', res.data.user.StaffLevel);
          // window.sessionStorage.setItem('salary', res.data.user.StaffSalary);
          // window.sessionStorage.setItem('remarks', res.data.user.StaffRemarks);
          // window.sessionStorage.setItem('telephone', res.data.user.StaffTelephone);
          this.$router.push({ path: "/Home" });
          return;
        }
        this.$message.error("后台错误");
      });
    },
  },
};
</script>

