import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import House_split from '../views/House_split'
import Select from '../views/Select.vue'
import Home_Consume from '../views/Home_Consume.vue'
const routes = [{
        path: '/Home_Consume',
        name: 'Home_Consume',
        component: Home_Consume,
        meta: {
            isShow: false,
        },
        children: [{
                meta: {
                    isShow: true,
                    title: '分房申请'
                },
                path: '/House_split',
                name: 'House_split',
                component: House_split
                    // component: () =>
                    //     import ( /* webpackChunkName: "login" */ '../views/House_split.vue'), //路由的软加载

            },
            {
                path: '/House_change',
                name: 'House_change',
                component: () =>
                    import ( /* webpackChunkName: "login" */ '../views/House_change.vue'), //路由的软加载
                meta: {
                    isShow: true,
                    title: '调房申请'
                }
            },
            {
                path: '/House_out',
                name: 'House_out',
                component: () =>
                    import ( /* webpackChunkName: "login" */ '../views/House_out.vue'), //路由的软加载
                meta: {
                    isShow: true,
                    title: '退房申请'
                }
            },
            {
                path: '/House_query',
                name: 'House_query',
                component: () =>
                    import ( /* webpackChunkName: "login" */ '../views/House_query.vue'), //路由的软加载
                meta: {
                    isShow: true,
                    title: '房产阈值查询'
                }
            },
            {
                path: '/Housing',
                name: '/Housing',
                component: () =>
                    import ( /* webpackChunkName: "login" */ '../views/Housing.vue'), //路由的软加载
                meta: {
                    isShow: true,
                    title: '查询分房结果'
                }
            }
        ]
    },
    {
        path: '/Login',
        name: 'Login',
        component: Login,
        meta: {
            isShow: false,
        }
        // component: () => import(/* webpackChunkName: "login" */ '..views/login.vue')//路由的软加载
    },
    {
        path: '/',
        name: 'Select',
        component: Select,
        meta: {
            isShow: false,
        }
        // component: () => import(/* webpackChunkName: "login" */ '..views/login.vue')//路由的软加载
    },
    {
        path: '/Home',
        name: 'Home',
        component: Home,
        meta: {
            isShow: false,
        },
        children: [{
                    meta: {
                        isShow: true,
                        title: '修改房产'
                    },
                    path: '/Admin_control',
                    name: 'Admin_control',
                    component: () =>
                        import ( /* webpackChunkName: "login" */ '../views/Admin_control.vue'), //路由的软加载
                    // component: () =>
                    //     import ( /* webpackChunkName: "login" */ '../views/House_split.vue'), //路由的软加载

                },
                {
                    path: '/Score_correct',
                    name: 'Score_correct',
                    component: () =>
                        import ( /* webpackChunkName: "login" */ '../views/Score_correct.vue'), //路由的软加载
                    meta: {
                        isShow: true,
                        title: '房产阈值修改'
                    }
                },
                {
                    path: '/Admin_split',
                    name: 'Admin_split',
                    component: () =>
                        import ( /* webpackChunkName: "login" */ '../views/Admin_split.vue'), //路由的软加载
                    meta: {
                        isShow: true,
                        title: '分房'
                    }
                }
            ]
            // component: () => import(/* webpackChunkName: "login" */ '..views/login.vue')//路由的软加载
    },



]

const router = createRouter({
    history: createWebHashHistory(),
    routes
})

export default router